@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,900;1,400&display=swap);
:root {
  /*
  -------------------------------------------
                color-variables
  -------------------------------------------
  */
  --color-g: rgb(73, 12, 22);
  --color-gl: #c5dbd4;
  --color-gd: #d30543;
  --color-gdd: #8d1e3a;
  --color-d: rgb(73, 12, 22);
  --color-f: rgb(241, 226, 229);

  /*
  -------------------------------------------
              font-size-variables
  -------------------------------------------
  */
  --text-base-size: 1em;
  --text-scale-ratio: 1.2;
  --text-xs: calc(
    (var(--text-base-size) / var(--text-scale-ratio)) / var(--text-scale-ratio)
  );
  --text-sm: calc(var(--text-xs) * var(--text-scale-ratio));
  --text-md: calc(
    var(--text-sm) * var(--text-scale-ratio) * var(--text-scale-ratio)
  );
  --text-lg: calc(var(--text-md) * var(--text-scale-ratio));
  --text-xl: calc(var(--text-lg) * var(--text-scale-ratio));
  --text-xxl: calc(var(--text-xl) * var(--text-scale-ratio));
  --text-xxxl: calc(var(--text-xxl) * var(--text-scale-ratio));

  /*
  -------------------------------------------
              Spacing-variables
  -------------------------------------------
  */
  --space-unit: 1em;
  --space-xxxxs: calc(0.125 * var(--space-unit));
  --space-xxxs: calc(0.25 * var(--space-unit));
  --space-xxs: calc(0.375 * var(--space-unit));
  --space-xs: calc(0.5 * var(--space-unit));
  --space-sm: calc(0.75 * var(--space-unit));
  --space-md: calc(1.25 * var(--space-unit));
  --space-lg: calc(2 * var(--space-unit));
  --space-xl: calc(3.25 * var(--space-unit));
  --space-xxl: calc(5.25 * var(--space-unit));
  --space-xxxl: calc(8.5 * var(--space-unit));
  --space-xxxxl: calc(13.75 * var(--space-unit));
}

* {
  box-sizing: border-box;
  font-family: "Lato";
  letter-spacing: calc(calc(0.25 * 1em) * 0.1);
  letter-spacing: calc(var(--space-xxxs) * 0.1);
}
body {
  /* position: relative;
  height: 100vh; */
  overflow-x: hidden;
  width: 100%;
}

.App {
  position: absolute;
  top: 0;
  text-align: center;
}

.navbar {
  position: relative;
  background-color: var(--color-d);
  width: 100%;
  box-shadow: 0px 0px 5px var(--color-f);
  z-index: 110 !important;
  height: 100px;
}
.navbar-brand {
  color: var(--color-f);
  display: flex !important;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
  margin-right: auto !important;
}
.navbar-brand p {
  color: var(--color-f) !important;
  margin-bottom: 0;
  font-style: italic;
  font-size: var(--text-md) !important;
  padding-left: var(--space-sm);
}
.navbar-brand:hover,
.navbar-brand:focus {
  color: var(--color-f) !important;
  cursor: default;
}
.navbar-brand img:hover {
  box-shadow: 3px 3px 5px var(--color-d);
  -webkit-animation: logo 2s 1;
          animation: logo 2s 1;
}
@-webkit-keyframes logo {
  100% {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
}
@keyframes logo {
  100% {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
}
.navbar img {
  border-radius: var(--text-xxl);
  width: 70px;
  height: 70px;
  display: inline-block;
}
.navbar p {
  color: var(--color-f);
  font-size: var(--text-lg);
}

/*
-------------------------------------------------
                 Media-Queries
-------------------------------------------------
*/

@media screen and (min-width: 900px) {
  .navbar {
    height: 12vh !important;
  }
  .navbar img {
    border-radius: var(--text-xxxl);
    width: 65px;
    height: 65px;
  }
  .navbar-brand p {
    font-size: var(--text-md);
  }
}

