.about-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
  /* z-index: 0; */
  color: var(--color-d);
  background-color: var(--color-f);
}
.about-inside {
  display: block;
  width: 100% !important;
  margin: auto;
}
.about-title {
  font-size: var(--text-lg);
  padding: var(--space-lg);
}
.about-exp {
  padding: 2%;
  font-size: var(--text-md);
}
.about-exp span a {
  color: inherit;
}
.about-project-items {
  display: block;
  background-color: var(--color-f);
}

.cv {
  text-align: center;
  align-items: center;
}
.cv-btn {
  position: relative;
  background-color: var(--color-f) !important;
  color: var(--color-d);
  width: 100%;
  top: -5rem;
}
.cv-btn:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

/*
-------------------------------------------------
                 Media-Queries
-------------------------------------------------
*/

@media screen and (min-width: 900px) {
  .about-wrapper {
    position: relative;
    top: -65px;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 5.5rem;
    width: 45%;
    height: 100%;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    margin: 0 auto 160px auto;
    padding: 7% 2% 8% 2%;
    transition: 0.7s all ease-in-out;
    z-index: 0;
  }
  .about-inside {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
    margin: auto;
  }
  .about-wrapper:hover {
    position: relative;
    -webkit-transform: scale(1.1) rotate(10deg);
            transform: scale(1.1) rotate(10deg);
    z-index: 10;
  }
  .about-wrapper:after {
    box-shadow: 10px 0 10px -10px #31331b inset;
    content: " ";
    height: 100%;
    position: absolute;
    top: 0;
    right: -10px;
    width: 10px;
    z-index: 1;
  }
  .about-title {
    font-size: var(--text-lg);
    padding: var(--space-sm);
  }
  .home-wrapper {
    height: 88vh !important;
    position: relative;
    overflow: hidden !important;
  }
  .about-project-items {
    display: flex;
    background-color: var(--color-f);
  }
  .cv {
    text-align: center;
    align-items: center;
  }
  .cv-btn {
    position: relative;
    background-color: var(--color-f) !important;
    color: var(--color-d);
    width: 100%;
    top: -5rem;
  }
}

.footer {
  position: relative;
  width: 100%;
  display: block;
  padding: var(--space-lg);
  background-color: var(--color-f);
  text-align: center;
  margin: auto;
}
.my-cv {
  width: 50%;
  background-color: var(--color-d) !important;
  color: var(--color-f) !important;
}
.my-cv-holder {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

/*
-------------------------------------------------
                 Media-Queries
-------------------------------------------------
*/

@media screen and (min-width: 900px) {
  .footer {
    position: absolute;
    bottom: 0;
    height: 13.6%;
    width: 100%;
    z-index: 200;
    display: block;
    text-align: center;
    background-color: var(--color-d);
    box-shadow: 2px 2px 10px var(--color-f);
  }
  .my-cv {
    background-color: var(--color-f) !important;
    color: var(--color-d) !important;
    position: relative;
    width: 25%;
  }
}

.project-btn {
  padding: 0;
  margin-bottom: 25px;
  border: none;
  outline: none;
  border-radius: var(--space-md);
  text-align: center;
  background-color: transparent;
  text-decoration: none;
  color: var(--color-d);
}
.project-btn:hover,
.project-btn:visited {
